module.exports = "<div class=section><title-link>Installation</title-link><div class=section__content><p>Vue MDL is only available through <code>npm</code>. Install it:<pre><code>npm install --save vue-mdl</code></pre></p><p>If you use a module bundler like<a href=\"https://webpack.github.io/\">webpack</a> or<a href=\"http://browserify.org/\">Browserify</a>, you can require it<pre><code class=javascript>var vmdl = require('vue-mdl');</code></pre>and use it. Otherwise you can include it in your html:<pre><code class=html><p>&lt;script src=&quot;//rawgit.com/posva/vue-mdl/master/dist/vue-mdl.js&quot;&gt;&lt;/script&gt;</p></code></pre>Add <code>.min</code> to get the minified version:<pre><code class=html><p>&lt;script src=&quot;//rawgit.com/posva/vue-mdl/master/dist/vue-mdl.min.js&quot;&gt;&lt;/script&gt;</p></code></pre></p><p>You'll also need to include <a href=http://www.getmdl.io/started/index.html target=_blank>Material Design Lite</a>. As it's not bundled with vue-mdl itself.</p></div></div>";


